.works {
  margin: 0% 7% 0% 7%;
}

.work {
  display: flex;
}

.worksbig {
  margin: 0;
}

.works h4 {
  font-size: 1.3rem;
  padding: 1rem 0rem;
}

.works h1 {
  padding: 0rem 1rem 1rem 1rem;
}

.work .col-1 {
  height: 180px;
  max-width: 246px;
}

.work .col-2 {
  padding: 0rem 1rem 0rem 1rem;
}

.work .col-2 h3 {
  font-size: 30px;
  font-weight: bold;
  line-height: 44px;
  color: var(--navyblue);
}

.work img {
  width: 250px;
  height: 100%;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s;
  image-rendering: -moz-crisp-edges;
  /* Firefox */
  image-rendering: -o-crisp-edges;
  /* Opera */
  image-rendering: -webkit-optimize-contrast;
  /* Webkit (non-standard naming) */
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
  /* IE (non-standard property) */
}

.work img:hover {
  opacity: 0.7;
}

.work .modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 2; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9); /* Black w/ opacity */
}
.work .modal img:hover {
  opacity: 1;
}
.work .modal-content {
  margin: auto;
  display: block;
  width: 80%;
  /* max-width: 700px; */
}

.work .caption {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: 150px;
}

.work .modal-content,
.caption {
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
}

@-webkit-keyframes zoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}

@keyframes zoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.work .close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}

.work .close:hover,
.work .close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

.work .col-2 .tech {
  color: var(--lightgray);
  font-size: 1.3rem;
  font-weight: normal;
  line-height: 29px;
  margin-left: 1.5rem;
}

.work .col-2 p {
  color: var(--navyblue);
  font-size: 18px;
  line-height: 23px;
  font-weight: 400;
}

.year-button {
  background-color: var(--yearButtonBlue);
  color: var(--white);
  border-radius: 16px;
  font-size: 18px;
  font-weight: bold;
  line-height: 60px;
  width: 62px;
  height: 25px;
  padding: 0rem 0.5rem 2px 0.5rem;
  text-align: center;
}

.horizontal-line {
  height: 0px;
  border: 0.5px solid var(--hline);
  max-width: 100%;
  margin: 1rem 0rem 1rem 0rem;
}

/* for mobile */

@media screen and (max-width: 768px) {
  .works {
    margin: 0;
  }
  .work {
    display: block;
  }
  .work .col-1 {
    height: 220px;
    max-width: 100%;
  }
  .work .col-1 img {
    max-width: 350px;
  }
  .work .col-2 h3 {
    font-size: 22px;
    padding-top: 0.5rem;
    padding-bottom: 0rem;
  }
  .work .col-2 {
    padding: 0;
  }
  .work .col-2 p {
    text-align: start;
    margin-top: 0.5rem;
    font-size: 16px;
  }
  .works h4 {
    padding: 1rem 0rem 1rem 0rem;
    text-align: center;
  }
  .year-button {
    line-height: 30px;
  }
  .work {
    margin: auto;
    width: 50%;
  }
  .work .modal-content {
    width: 100%;
  }
}

@media screen and (max-width: 540px) {
  .work {
    margin: auto;
    width: 60%;
  }
}

@media screen and (max-width: 450px) {
  .work {
    margin: auto;
    width: 85%;
  }
  .work img {
    width: 100%;
  }
}
